import { Component } from '@angular/core';
import { HttpClient, HttpParams, HttpHeaders } from '@angular/common/http';
import { Observable } from 'rxjs';

@Component({
  selector: 'app-root',
  templateUrl: './app.component.html',
  styleUrls: ['./app.component.css']
})
export class AppComponent {

  public URL = 'https://dbpedia.org/sparql';
  private propertiesJSON = './assets/properties.json';
  private propertyMatching;
  public sparkQlData;
  public sparkQlQuery = '';
  public sparkQlQueryPerson = '';
  public sparkQlQueryThing = '';
  public userInput = '';
  public input = '';
  private argument: string[];
  private prefixes =
    'PREFIX foaf: <http://xmlns.com/foaf/0.1/>\n' +
    'PREFIX rdf: <http://www.w3.org/1999/02/22-rdf-syntax-ns#>\n' +
    'PREFIX rdfs: <http://www.w3.org/2000/01/rdf-schema#>\n' +
    'PREFIX dbo: <http://dbpedia.org/ontology/>\n' +
    'PREFIX dbp: <http://dbpedia.org/property/>\n' +
    'PREFIX dbr: <http://dbpedia.org/resource/>\n' +
    'PREFIX dct: <http://purl.org/dc/terms/>\n'
    ;

  constructor(
    private http: HttpClient
  ) {
    this.getJSON().subscribe(data => {
      this.propertyMatching = data;
      console.log(this.propertyMatching);
    });
    console.log(this.propertyMatching);
  }

  public getJSON(): Observable<any> {
    return this.http.get(this.propertiesJSON);
  }

  userInputListener(userInput) {
    const temp = userInput.value;
    this.argument = temp.split(' ');
    let argument1;
    let argument2;
    let argument3;

    if (this.argument.length > 3) {
      for (let i = 0; i < this.argument.length - 2; i++) {
        if (i === 0) {
          argument1 = this.argument[i];
        } else if (i > 0 && argument1) {
          argument1 = argument1 + ' ' + this.argument[i];
        }
      }
      argument2 = this.argument[this.argument.length - 2];
      argument3 = this.argument[this.argument.length - 1];
      this.input = argument1 + ' ' + argument2 + ' ' + argument3;
      this.sparkQlGenerator(argument1, argument2, argument3);
    } else if (this.argument[0] && this.argument[1] && this.argument[2]) {
      argument1 = this.argument[0];
      argument2 = this.argument[1];
      argument3 = this.argument[2];
      this.input = argument1 + ' ' + argument2 + ' ' + argument3;
      this.sparkQlGenerator(argument1, argument2, argument3);
    } else {
      this.sparkQlQuery = 'Please insert a valid question.';
    }
  }

  sparkQlGenerator(argument1, argument2, argument3) {
    const sparkQl = this.prefixes + 'SELECT ' + argument3 + ' WHERE ' +
      '{ '
      + '?result dbo:' + argument2 + ' ' + argument3 + ' . '
      + '?result rdfs:label "' + argument1 + '"@en . '
      + 'FILTER(lang(' + argument3 + ')="en" || datatype(' + argument3 + ') = xsd:string)'
      + '} LIMIT 1 ';

    this.sparkQlQuery = sparkQl;
    this.sparkQlRequest(sparkQl);
  }

  sparkQlRequest(sparkQlQuery) {
    const headers = new HttpHeaders()
      .set('Content-Type', 'application/json');

    const params = new HttpParams()
      .set('default-graph-uri', 'http://dbpedia.org')
      .set('query', sparkQlQuery)
      .set('format', 'application/json')
      .set('CXML_redir_for_subjs', '121')
      .set('CXML_redir_for_hrefs=', '')
      .set('timeout', '30000')
      .set('run', '+Run+Query+');

    const httpOptions = {
      headers: headers,
      params: params
    };

    this.http.get(this.URL, httpOptions)
      .subscribe(
        data => {
          console.log('GET Request is successful: ', data);
          this.sparkQlData = data;
        },
        error => {
          console.log('Error:', error);
          this.sparkQlData = error;
        }
      );
  }
}
